.pageWrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 6.25vh 7.656vw;
  box-sizing: border-box;
}

.header {
  width: 100%;
  /* max-width: 1080px; */
  margin-bottom: 24px;
}

.title {
  margin: 0;
  font-size: 100px;
  font-weight: 800;
  margin-bottom: 32px;
  /* text-align: center; */
}

.link {
  font-size: 50px;
  margin-left: 8px;
  color: #1976d2;
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}

.main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}

.section {
  width: 100%;
}

.sectionTitle {
  margin: 0;
  font-size: 50px;
  font-weight: 800;
  margin-bottom: 24px;
  /* text-align: center; */
}

.demoCaption {
  margin-bottom: 8px;
  text-align: center;
  font-size: 40px !important;
  font-weight: 200 !important;
  color: #878787 !important;
}

.mediaGrid {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  justify-content: center;
}

.image {
  width: 320px;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  background: #f2f2f2;
}

.mainVideo {
  width: 100%;
  aspect-ratio: 16/9;
  background: #f2f2f2;
  margin-bottom: 8vh;
}

.video {
  width: 360px;
  height: 220px;
  border-radius: 8px;
  background: #000;
}

.italic {
  font-style: italic;
  font-weight: 200;
  font-size: 30px;
  line-height: 100%;
  color: #555555;
}

