.pageWrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 6.25vh 7.656vw;
  box-sizing: border-box;
}

.header {
  width: 100%;
  margin-bottom: 32px;
  text-align: center;
}

.title {
  margin: 0;
  font-size: 50px;
  font-weight: 800;
  margin-bottom: 32px;
}

.desc {
  margin: 0;
  color: #666;
}

.main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section {
  width: 100%;
}

.mediaGrid {
  display: grid;
  width: 100%;
  gap: 24px;
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.gridItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
}

.video {
  width: 100%;
  aspect-ratio: 16 / 9;
  background: #000;
  border-radius: 8px;
}

.caption {
  margin: 0;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
}

@media (max-width: 1200px) {
  .mediaGrid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 768px) {
  .mediaGrid {
    grid-template-columns: 1fr;
  }
}


